body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  background: #111;
}
section {
  background: #111;
  min-height: 100vh;
}
section .bannerVideo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
section .bannerVideo video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
}
section .bannerVideo video.active {
  opacity: 1;
}
.container {
  position: relative;
  padding: 0 100px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 100vh;
}
.container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  -moz-backdrop-filter: blur(10px);
  -o-backdrop-filter: blur(10px);
  -ms-backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  box-shadow: 10px 0 15px rgba(0, 0, 0, 0.05);
}
.container header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 100px;
}
.container header .logo {
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  font-size: 1.8em;
}
.container header ul {
  position: relative;
  display: flex;
}
.container header ul li {
  margin: 0 20px;
}
.container header ul li a {
  position: relative;
  font-size: 18px;
  padding: 4px 10px;
  color: #fff;
  transition: all 0.3s ease-in-out;
}
.container header ul li a:hover,
.container header ul li a.active {
  background: #fff;
  color: #111;
}
.container .content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.bannerText {
  position: relative;
  max-width: 700px;
}
.bannerText__block {
  display: none;
  transition: 0.5s ease-in-out;
}
.bannerText__block.active {
  display: initial;
}
.bannerText__block h2 {
  color: #fff;
  font-size: 6em;
  line-height: 1em;
  font-weight: 700;
}
.bannerText__block p {
  color: #fff;
  font-size: 18px;
  margin: 20px 0;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.sci {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
}
.sci li a {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
}
.sci li a:hover {
  background: #d3d1d1;
}
.sci li a img {
  display: inline-block;
  max-width: 24px;
}
.controls {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
}
.controls li {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.controls li:hover {
  background: #d3d1d1;
}
.menuIcon {
  display: none;
}
@media (max-width: 991px) {
  .container {
    padding: 20px;
  }
  .container header {
    padding: 10px 20px;
  }
  .bannerText__block h2 {
    font-size: 3em;
  }
  .bannerText {
    text-align: center;
  }
}
@media (max-width: 767px) {
  .container::before {
    width: 100%;
  }
  .container header ul {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    z-index: 1000;
    display: none;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .container header ul li {
    text-align: center;
    margin: 10px;
  }
  .container header ul li a {
    color: #333;
    font-size: 1.5em;
  }
  .container header ul li a:hover {
    color: #f00;
  }
  .container header.active ul {
    display: flex;
  }
  .menuIcon {
    position: fixed;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    display: initial;
    z-index: 10000;
    background: #fff url(../img/menu.webp);
    background-size: 30px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: center;
  }
  .menuIcon.active {
    background: #fff url(../img/close.webp);
    background-size: 25px;
    cursor: pointer;
    background-repeat: no-repeat;
    background-position: center;
  }
  .sci li a {
    width: 50px;
    height: 50px;
  }
  .controls li {
    width: 60px;
    height: 60px;
  }
}
